          </svg>
        </span>
      </div>
    </div>
    <div class="svg_list">
      <ul v-if="svgList.length > 0">
        <li v-for="item in svgList" :key="item.id">
          <div class="shadow"></div>
          <div class="svg_name">{{item.svg_name}}</div>
          <div class="hover_over">
            <span @click="download(item.id)">
              <img src="@/assets/img/download.svg" alt="" width="25" height="25">
