        <span>暂无数据</span>
      </div>
    </div>
    <modal :title="'编辑信息'" :visible="visible" @cancel="cancel" @confirm="confirm">
      <Row class="edit_row">
        <Col span="6" >名称</Col>
        <Col span="18"><Input v-model="coll_name" placeholder="请输入..." :maxlength="16"></Input></Col>
      </Row>
    </modal>
    <modal :title="'操作提示'" :visible="delVisible" @cancel="delCancel" @confirm="delConfirm">
      <span>确定删除收藏{{pName}}</span>
    </modal>
  </div>
</template>

<script>
import { Message } from "iview";
import {collectionSvgList, editUserCollections, delUserCollections, collectionSvg} from '../../network/operation/index'
import Modal from '@/components/Modal.vue'

export default {
  name: 'svglist',
  data() {
    return {
      uid: '',
      svgList: [],
      cid: "",
      pName: "",
      tagList: [],
      visible: false,
      coll_name: '',
      delVisible: false,
      delId: '',
      delName: '',
      sign: ''
    };
  },
  components: {
    Modal,
  },
  created() {
    this.uid = JSON.parse(localStorage.getItem('userInfo')).id
    this.pName = this.$route.params.pName
    this.init(this.$route.params.id)
    this.$EventBus.$on("getCid", (cid) => {
      this.init(cid);
    });
  },
  mounted() {

  },
  watch: {
    svgList: function(newList) {
      this.$nextTick(() => {
        const els = document.querySelectorAll(`.shadow`)
        for (let i = 0;i < newList.length;i++) {
          if (els[i] === undefined) {
            return
          }
          if (!els[i].shadowRoot) {
            const shadow = els[i].attachShadow({mode: 'open'})
            const el = document.createElement('div')
            const styles = document.createElement('style')
            styles.textContent = `svg {width: 120px;height:120px}`
            el.style.pointerEvents = 'none'
            el.style.width = `45px`
            el.style.height = `45px`
            el.innerHTML = newList[i].svg_code
            shadow.appendChild(el)
          }
        }
      }) 
    }
  },
  deactivated() {
    this.svgList = []
  },
  methods: {
    // 初始化
    init(cid) {
      if (!cid) {
        this.$router.push({name: 'index'})
        return 
      }
      this.cid = cid;
      if (JSON.parse(localStorage.getItem('userInfo'))) {
        this.uid = JSON.parse(localStorage.getItem('userInfo')).id
      }
      // this.getPackage();
      this.getList();
    },
    // 获取svg列表
    getList() {
      collectionSvgList(this.cid)
        .then((res) => {
          if (res.status == 200) {
            this.svgList = res.data;
          } else {
            this.$toast.show("请求失败，请重试");
          }
        })
        .catch((err) => {
          this.$toast.show("请求错误，请重试");
        });
    },
    editCollection() {
      this.visible = true
      this.coll_name = this.pName
    },
    cancel() {
      this.visible = false
    },
    confirm() {
      editUserCollections(this.cid, this.coll_name, this.uid).then(res => {
        this.visible = false
        if (res.status == 200) {
          this.$toast.show('修改成功');
          this.getList();
        } else {
          this.$toast.show('请求失败，请重试');
        }
      }).catch(err => {
        this.$toast.show(err.response.data.message || "请求错误，请重试");
      })
    },
    // 删除收藏
    del(id, name, sign) {
      this.delId = id
      this.delName = name
      this.sign = sign
      this.delVisible = true
    },
    delCancel() {
      this.delVisible = false
    },
    delConfirm() {
      if (this.sign == 'collection') {
        delUserCollections(this.cid).then(res => {
          this.delVisible = false
          if (res.status == 200) {
            this.$toast.show('删除成功');
            this.getList();
          } else {
            this.$toast.show('请求失败，请重试');
          }
        }).catch(err => {
          this.$toast.show(err.response.data.message || "请求错误，请重试");
        })
      } else {
        collectionSvg(this.cid, this.delId).then(res => {
          this.delVisible = false
          if (res.status == 200) {
            this.$toast.show('删除成功');
            this.getList();
          } else {
            this.$toast.show('请求失败，请重试');
          }
        }).catch(err => {
          this.$toast.show(err.response.data.message || "请求错误，请重试");
        })
      }
    },
    close() {
      this.addSvgVisible = false
    },
    download(id) {
      const a = document.createElement("a");
      const url = `${process.env.VUE_APP_BASE_URL}api/svg/download/?pid=${id}`;
      a.href = url;
      a.download = "123.svg";
      document.body.appendChild(a);
      a.click();
      window.URL.revokeObjectURL(url);
      document.body.removeChild(a);
    },
  },
};
</script>

<style lang="less" scoped>
#collection_wrapper {
  display: flex;
  flex-direction: column;
  color: var(--theme-color);
  padding: 20px 40px;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: var(--theme-bg-color);
  @media screen and (max-width: 510px) {
    padding: 20px;
  }
  &.overlay {
    pointer-events: none;
    transition: 0.3s;
    background-color: var(--overlay-bg);
  }
}
.title {
  display: flex;
  justify-content: space-between;
  margin: 12px 0;
  div {
    font-size: 32px;
  }
  >div:last-child {
    cursor: pointer;
  }
  .count {
    font-size: 14px;
    width: 20px;
    height: 20px;
    display: inline-block;
    text-align: center;
    line-height: 20px;
    margin-left: 8px;
    border-radius: 50%;
    background-color: #3a6df0;
    color: #fff;
  }
}
.svg_list {
  width: 100%;
  height: 100vh;
  margin-top: 20px;
  ul {
    display: flex;
    flex-wrap: wrap;
    li {
      display: flex;
      justify-content: center;
      align-items: center;
      list-style: none;
      position: relative;
      float: left;
      margin: 15px 13px;
      width: 105px;
      height: 115px;
      text-align: center;
      position: relative;
      overflow: visible;
      border: 1px dashed transparent;
      border-radius: 5px;
      background-color: rgba(255, 255, 255, 0.2);
      transition: all 0.3s ease-in-out;
      cursor: pointer;
      &:hover {
        transform: translateY(-8px) scale(1.1);
        box-shadow: 0 0 5px #eee;
      }
      .svg_name {
        position: absolute;
        bottom: 0;
      }
      .hover_over {
        position: absolute;
        display: flex;
        justify-content: space-around;
        flex-direction: column;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        background: rgba(13, 10, 49, 0.9);
        border-radius: 5px;
        opacity: 0;
        transition: all 0.2s ease-in;
        color: #fff;
        &:hover {
          opacity: 1;
        }
      }
    }
  }
}
.edit {
  margin-top: 50%;
}
.edit_row {
  font-size: 14px!important;
  margin: 10px 0;
}
.taglist {
  display: flex;
  flex-wrap: wrap;
}
.drawer {
  .data_wrapper {
    padding: 30px;
    max-height: 200px;
    overflow: auto;
    ul {
      display: flex;
      flex-wrap: wrap;
      li {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 70px;
        height: 70px;
        margin-right: 12px;
        margin-bottom: 12px;
        border: 1px solid #ccc;
        border-radius: 5px;
        list-style: none;
        font-size: 12px;
        .del {
          position: absolute;
          display: flex;
          justify-content: center;
          align-items: center;
          top: -5px;
          right: -5px;
          width: 14px;
          height: 14px;
          border-radius: 50%;
          color: #000;
          background-color: #ccc;
        }
      }
    }
    > div {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 70px;
      height: 70px;
      border: 1px solid #ccc;
      border-radius: 3px;
      font-size: 20px;
      cursor: pointer;
      span {
        cursor: pointer;
      }
      input {
        display: none;
      }
    }
  }
  .info {
    padding: 12px 30px;
    font-size: 18px;
    .key {
      font-weight: bold;
    }
    .description {
      font-size: 16px;
      border-bottom: none;
    }
    .info_item {
      margin: 12px 0;
    }

    .tag {
      margin: 0;
      padding-top: 5px;
      span:last-child {
        margin-right: 0;
      }
    }
  }
}
.line {
  width: 100%;
  margin: 18px 0;
  border-bottom: 1px solid #ccc;
}
.no_content {
  width: 100%;
  height: 555px;
  align-items: center;
  display: flex;
  flex-direction: column;
  color: var(--theme-color);
  padding-top: 135px;
  svg {
    width: 125px;
    height: 125px;
    fill: var(--theme-color);
  }
}
</style>
